/* Footer container */
.container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  /* Grid layout */
  .grid {
    display: grid;
    gap: 20px;
  }
  
  .grid-two-column {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .grid-four-column {
    grid-template-columns: repeat(4, 1fr);
  }
  
  /* Contact section */
  .contact-short {
    padding: 50px 0;
    background-color: #f8f9fa;
  }
  
  .contact-short h3 {
    margin: 0;
  }
  
  /* Footer section */
  footer {
    padding: 30px 0;
    background-color: #0a1435;
    color: #fff;
  }
  
  .footer-about p {
    margin: 10px 0;
  }
  
  .footer-subscribe form {
    display: flex;
    align-items: center;
  }
  
  .footer-subscribe input[type="email"],
  .footer-subscribe input[type="submit"] {
    padding: 10px;
    margin: 5px 0;
  }
  
  .footer-subscribe input[type="submit"] {
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .footer-social--icons {
    display: flex;
  }
  
  .footer-social--icons .icons {
    font-size: 24px;
    margin-right: 10px;
  }
  
  .footer-contact h3 {
    margin: 10px 0;
  }
  
  .footer-bottom--section {
    padding-top: 20px;
    border-top: 1px solid #fff;
  }
  
  .footer-bottom--section p {
    margin: 0;
  }
  
  .footer-bottom--section p:last-child {
    margin-top: 10px;
  }
  
  /* Responsive styles */
  @media screen and (max-width: 768px) {
    .grid-four-column {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  