.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  
  .hero-section-data {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .intro-data {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .hero-section-data p {
    margin-bottom: 20px;
  }
  
  .hero-section-data button {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .hero-section-data button:hover {
    background-color: #0056b3;
  }
  
  .hero-section-image img {
    width: 100%;
    height: auto;
  }
  