





  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #f8f9fa;
  }
  
  .navbar-links {
    display: flex;
  }
  
  .navbar-link {
    margin-right: 20px;
    color: #333;
    text-decoration: none;
  }
  
  .navbar-link:hover {
    color: #555;
  }
  
  .navbar-cart {
    display: flex;
    align-items: center;
    margin-left: auto; /* Set margin-left to push the cart icon to the right */
  }
  
  .cart-trolley {
    font-size: 1.5rem;
    margin-right: 5px;
  }
  