.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .intro-data {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .common-heading {
    font-size: 2rem;
    margin-bottom: 30px;
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
  
  .cart-item-details {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 20px;
  }
  
  .cart-item-details img {
    max-width: 100%;
    height: auto;
  }
  
  .cart-item-details h3 {
    margin-top: 0;
  }
  
  .cart-item-details p {
    margin-bottom: 10px;
  }
  
  .cart-item-details .price {
    font-weight: bold;
  }
  
  .cart-item-details button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .cart-item-details button:hover {
    background-color: #0056b3;
  }
  