.product-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Display three columns */
  gap: 20px; /* Add gap between columns */
  padding: 20px;
}

.product-title {
  margin-bottom: 10px;
}

.product-image img {
  width: 100%;
  height: auto; /* Maintain aspect ratio */
  object-fit: cover;
}

.product-price {
  margin-top: 10px;
}
